<template>
  <div
      style="margin-top: 50px">
    <draggable v-model="category"
               group="category"
               style="width: 100%"
               :disabled="!isEdit"
               @start="handleStartDrag($event.oldIndex, category)"
               @end="handleEndDrag"
               v-bind="getCategoryOptions()"
               @change="categoryChange">
      <el-row :gutter="10" v-for="(item, index) in category" :key="index" class="text-black">
        <el-divider content-position="left">
          <b>{{ item.title }}</b>
        </el-divider>
        <draggable v-model="item.data"
                   group="people"
                   style="width: 100%"
                   :disabled="!isEdit"
                   @start="handleStartDrag($event.oldIndex, item.data)"
                   @end="handleEndDrag"
                   v-bind="getOptions()">
          <el-col :lg="4" :md="6" :sm="8" :xs="12"
                  v-for="(detail, index) in item.data"
                  :key="index"
                  style="margin-bottom: 10px">
            <div>
              <el-card shadow="hover"
                       class="linkCard"
                       :body-style="{padding: '10px', height: '80px'}"
                       style="height: 100px; width: 100%; user-select: none; cursor: pointer; border-radius: 6px;">
                <div style="display: flex; flex-flow: column; justify-content: space-between; height: 80px"
                     @click.stop="click(detail)">
                  <div style="display: flex">
                    <el-image
                        v-if="detail.logo"
                        :src="detail.logo"
                        :style="{backgroundColor: detail.logoColor ? detail.logoColor : '#FFF'}"
                        style="width: 30px; height: 30px; margin-right: 5px; border-radius: 8px;"/>
                    <span
                        class="title-color"
                        style="display: inline-block; line-height: 30px; font-size: 14px">{{
                        detail.name
                      }}</span>
                  </div>
                  <span class="desc-color" style="font-size: 15px">{{ detail.desc }}</span>
                </div>
              </el-card>
            </div>
          </el-col>
        </draggable>
        <el-col :lg="4" :md="6" :sm="8" :xs="12"
                class="no-drag"
                v-if="!isDragging && isEdit"
                style="margin-bottom: 10px">
          <div
              class="linkAddButton"
              @click.stop="handleShowLinkAddDrawer(item)">
            <el-card shadow="hover"
                     :body-style="{padding: '10px', height: '80px'}"
                     style="height: 100px; width: 100%; user-select: none; cursor: pointer; border-radius: 6px; border: 2px dashed #2196F3">
              <div
                  style="display: flex; flex-flow: column; justify-content: center; align-items: center; height: 80px">
                <i class="el-icon-circle-plus" style="font-size: 40px; display: inline-block; color: #2196F3"/>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>
    </draggable>
    <el-card shadow="hover"
             v-if="!isDragging && isEdit"
             :body-style="{padding: '0px', height: '0'}"
             class="no-drag categoryAddButton"
             style="height: 35px; width: 100%; user-select: none; cursor: pointer; border-radius: 6px; border: 2px dashed #2196F3">
      <div
          @click.stop="handleShowCategoryAddDrawer"
          style="font-style: italic; font-size: 14px;display: flex; flex-flow: row; justify-content: center; align-items: center; line-height: 30px; color: #2196F3; font-weight: 500">
        <i class="el-icon-circle-plus"></i>
        <span>新增分类</span>
      </div>
    </el-card>
    <el-drawer
        :title="(currentCategory? currentCategory.title : '' )+ '-新增链接卡片'"
        :append-to-body="true"
        :visible.sync="visibleLinkAdd">
      <el-form ref="linkForm"
               :model="linkForm"
               :rules="linkFormRules"
               style="padding: 0 20px 0 0"
               label-width="80px">
        <el-form-item label="分类" prop="title">
          <el-input disabled v-model="linkForm.title"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="linkForm.name"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="desc">
          <el-input v-model="linkForm.desc"></el-input>
        </el-form-item>
        <el-form-item label="链接" prop="href">
          <el-input v-model="linkForm.href"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="logo">
          <el-upload
              class="avatar-uploader"
              action="#"
              :auto-upload="false"
              :show-file-list="false"
              :on-change="handlePreview"
              accept="image/*">
            <img v-if="linkForm.logo" :src="linkForm.logo" class="avatar" alt="image">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="预览">
          <el-card shadow="hover" :body-style="{padding: '10px', height: '80px'}"
                   style="height: 100px; width: 200px; user-select: none; cursor: pointer; border-radius: 6px;">
            <div style="display: flex; flex-flow: column; justify-content: space-between; height: 80px"
                 @click.stop="click(linkForm, true)">
              <div style="display: flex">
                <el-image
                    v-if="linkForm.logo"
                    :src="linkForm.logo"
                    :style="{backgroundColor: linkForm.logoColor ? linkForm.logoColor : '#FFF'}"
                    style="width: 30px; height: 30px; margin-right: 5px; border-radius: 8px;"/>
                <span
                    class="title-color"
                    style="display: inline-block; line-height: 30px; font-size: 14px">{{
                    linkForm.name
                  }}</span>
              </div>
              <span class="desc-color" style="font-size: 15px">{{ linkForm.desc }}</span>
            </div>
          </el-card>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleSaveLink">保存</el-button>
          <el-button @click="visibleLinkAdd = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
    <el-drawer
        title="新增分类卡片"
        :append-to-body="true"
        :visible.sync="visibleCategoryAdd">
      <el-form ref="categoryForm"
               :model="categoryForm"
               :rules="categoryFormRules"
               style="padding: 0 20px 0 0"
               label-width="80px">
        <el-form-item label="标题" prop="title">
          <el-input v-model="categoryForm.title"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSaveCategory">保存</el-button>
          <el-button @click="visibleCategoryAdd = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
    <transition name="slide-up">
      <div
          v-show="isDragging && isEdit"
          class="delete-zone"
          :class="{ 'delete-active': isOverDeleteZone }"
          @dragover.prevent="handleDragOver"
          @dragleave="handleDragLeave"
      >
        <i class="el-icon-delete"></i>
        <span v-if="!isOverDeleteZone">拖拽到此删除</span>
        <span v-if="isOverDeleteZone">释放删除</span>
      </div>
    </transition>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import Compressor from 'compressorjs'

export default {
  name: "info-nav",
  components: {
    draggable,
  },
  props: {
    navData: {
      type: Array,
      default: () => []
    },
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    navData: {
      handler(newValue, oldValue) {
        this.category = newValue
      },
      deep: false
    }
  },
  created() {

  },
  data() {
    return {
      categoryForm: {
        title: null
      },
      categoryFormRules: {
        title: [
          {required: true, message: '请输入分类名称', trigger: 'blur'},
        ],
      },
      linkForm: {
        name: null,
        desc: null,
        logo: null,
        href: null
      },
      linkFormRules: {
        name: [
          {required: true, message: '请输入名称', trigger: 'blur'},
        ],
        desc: [
          {required: true, message: '请输入描述', trigger: 'blur'},
        ],
        href: [
          {required: true, message: '请输入链接', trigger: 'blur'},
        ],
      },
      visibleLinkAdd: false,
      visibleCategoryAdd: false,
      currentCategory: null,
      category: [],
      isDragging: false,
      isOverDeleteZone: false,
      currentDraggedItem: null,
    }
  },
  mounted() {

  },
  beforeDestroy() {

  },
  methods: {
    handleShowLinkAddDrawer(category) {
      this.visibleLinkAdd = true;
      this.currentCategory = category;
      this.linkForm.title = category.title
      this.linkForm.logo = null
      this.linkForm.name = null
      this.linkForm.desc = null
      this.linkForm.href = null
    },
    handleShowCategoryAddDrawer() {
      this.visibleCategoryAdd = true
      this.categoryForm.title = null
    },
    handleSaveCategory() {
      this.$refs['categoryForm'].validate((valid) => {
        if (valid) {
          this.category.push({
            title: this.categoryForm.title,
            data: []
          })
          this.visibleCategoryAdd = false
          this.$emit('update:navData', this.category)
        } else {
          return false;
        }
      });
    },
    handleSaveLink() {
      this.$refs['linkForm'].validate((valid) => {
        if (valid) {
          this.currentCategory.data.push({
            name: this.linkForm.name,
            desc: this.linkForm.desc,
            href: this.linkForm.href,
            logo: this.linkForm.logo,
          })
          this.visibleLinkAdd = false
          this.$emit('update:navData', this.category)
        } else {
          return false;
        }
      });
    },
    handlePreview(file) {
      new Compressor(file.raw, {
        maxWidth: 100, // 最大宽度
        maxHeight: 100, // 最大高度
        quality: 1, // 压缩质量，范围是0到1，1表示最高质量，0表示最低质量。
        mimeType: 'image/jpeg',
        success: (result) => {
          // 压缩后的图片blob对象可以在这里使用或上传。
          const reader = new FileReader()
          reader.onload = (e) => {
            this.linkForm.logo = e.target.result
          }
          reader.readAsDataURL(result)
        },
        error(err) {
          console.log(err.message);
        },
      });
      return false
    },
    handleStartDrag(index, list) {
      this.isDragging = true
      this.currentDraggedItem = list[index]
    },

    handleEndDrag(e) {
      if (this.isOverDeleteZone) {
        this.executeDelete()
      }
      this.resetDragState()
    },

    // 拖拽进入删除区
    handleDragOver() {
      this.isOverDeleteZone = true
    },

    // 拖拽离开删除区
    handleDragLeave() {
      this.isOverDeleteZone = false
    },

    // 释放删除
    executeDelete() {
      if (!this.currentDraggedItem) {
        return
      }
      if (this.currentDraggedItem.data) {
        let index = this.category.findIndex(item => item === this.currentDraggedItem);
        if (index !== -1) {
          this.category.splice(index, 1)
        }
      } else {
        this.category.forEach(item => {
          if (item.data) {
            let index = item.data.findIndex(item => item === this.currentDraggedItem);
            if (index !== -1) {
              item.data.splice(index, 1)
            }
          }
        })
      }
      this.categoryChange()
    },

    // 重置状态
    resetDragState() {
      this.isDragging = false
      this.isOverDeleteZone = false
      this.currentDraggedItem = null
    },
    categoryChange() {
      this.$emit('update:navData', this.category);
    },
    getCategoryOptions() {
      return {
        animation: 400,
        chosenClass: 'dragging-item-category',
        dragClass: 'dragging-item-category-drag',
        filter: '.no-drag',
      }
    },
    getOptions() {
      return {
        animation: 400,
        chosenClass: 'dragging-item',
        filter: '.no-drag'
      }
    },
    click(item, force) {
      if (this.isEdit && !force) {
        return
      }
      if (!item.href) {
        return;
      }
      // 验证是否是 http:// 与 https:// 前缀
      if (!item.href.startsWith('http://') && !item.href.startsWith('https://')) {
        return;
      }
      let link = document.createElement("a")
      link.href = item.href
      link.setAttribute("target", "_blank")
      link.click();
    }
  }
}
</script>

