import {driver} from "driver.js";
import "driver.js/dist/driver.css";
import "@/styles/driver.scss"
import Vue from "vue";
function initDriver() {
  Vue.prototype.$driver = new driver({
    opacity: 0.75, // 背景不透明度
    stagePadding: 6,
    allowClose: false, // 禁止点击外部关闭
    doneBtnText: '完成', // 完成按钮标题
    closeBtnText: '关闭', // 关闭按钮标题
    stageBackground: '#fff', // 引导对话的背景色
    nextBtnText: '下一步', // 下一步按钮标题
    prevBtnText: '上一步', // 上一步按钮标题
    // animate: false
    showButtons: ["next", "previous"],
    popoverClass: 'driverjs-theme',
    onHighlightStarted(element, step, opts) {
      element.classList.add('no-click');
    },
    onDeselected(element, step, opts) {
      element.classList.remove('no-click');
    },
    steps: [
      {
        element: '.linkCard:nth-child(1)',
        popover: {
          title: '链接卡片',
          description: '点击具体卡片可跳转页面',
          position: 'right'
        }
      },
      {
        element: '#navEditButton',
        popover: {
          title: '启用编辑模式',
          description: '启用编辑后，可以对链接卡片动态配置',
          position: 'right',
          onNextClick(element, step, opts) {
            Vue.prototype.$bus.$emit('navEditButtonClick')
            Vue.prototype.$nextTick(() => {
              opts.driver.moveNext()
            })
          }
        },
      },
      {
        element: '#navMain',
        popover: {
          title: '卡片编辑',
          description: '可拖拽卡片与分类动态调整类型',
          position: 'right',
          onPrevClick(element, step, opts) {
            Vue.prototype.$bus.$emit('navCancelButtonClick');
            Vue.prototype.$nextTick(() => {
              opts.driver.movePrevious()
            })
          },
        },
        onHighlightStarted(element, step, opts) {
          element.classList.add('no-click-exclude-nav-main');
        },
        onDeselected(element, step, opts) {
          element.classList.remove('no-click-exclude-nav-main');
        },
      },
      {
        element: '.linkAddButton:nth-child(1)',
        popover: {
          title: '新增链接卡片',
          description: '在当前分类下新增链接卡片',
          position: 'right'
        },
      },
      {
        element: '.categoryAddButton',
        popover: {
          title: '新增链接分类',
          description: '在当前分类下新增链接分类',
          position: 'right'
        },
      },
      {
        element: '#navEditTitleButton',
        popover: {
          title: '修改标题',
          description: '点击可修改当前导航标题',
          position: 'right'
        },
      },
      {
        element: '#navSaveButton',
        popover: {
          title: '保存变更',
          description: '点击保存对导航内容的变更',
          position: 'right'
        },
      },
      {
        element: '#navCancelButton',
        popover: {
          title: '取消变更',
          description: '点击还原导航内容',
          position: 'right',
          onNextClick(element, step, opts) {
            Vue.prototype.$bus.$emit('navCancelButtonClick');
            Vue.prototype.$nextTick(() => {
              opts.driver.destroy()
            })
            localStorage.setItem('driver-init', "true")
          },
        },
      }
    ]
  })
}

if (localStorage.getItem('driver-init') !== "true") {
  initDriver();
}

export {
  initDriver
}