<template>
  <div style="height: 100vh; width: 100vw; overflow-y: hidden">
    <el-scrollbar style="height: 100%; width: 100vw" wrap-style="overflow-x:hidden;">

      <div class="nav_top_style"
      >
        <div class="nav_title">
          <div class="flex" style="justify-content: space-between; align-items: center;">
            <div class="flex" style="flex-direction: row;align-items: center;justify-content: flex-start;">
              <img src="/favicon.ico" width="20" height="20"/>
              <span class="nav_title_name">{{ config.title }}</span>
            </div>
            <div style="display: flex;align-items: center;justify-content: flex-end; height: 50px;">
              <div
                  v-if="!isEdit"
                  style="align-items: center"
                  type="button" class="flex DocSearch DocSearch-Button">
                <i class="el-icon-user-solid"></i>
                <span class="DocSearch-Button-Container">访问计数：{{ config.count }}</span>
              </div>
              <button
                  id="navEditButton"
                  v-if="!isEdit"
                  @click="editConfig"
                  type="button" class="DocSearch DocSearch-Button DocSearch-Button-Hover" aria-label="编辑模式">
                <i class="el-icon-edit-outline"></i>
                <span class="DocSearch-Button-Container">编辑模式</span>
              </button>
              <EditTitle
                  id="navEditTitleButton"
                  :data.sync="config.title"
                  :is-edit="isEdit"/>
              <button
                  id="navSaveButton"
                  v-if="isEdit"
                  @click="pushConfig"
                  type="button" class="DocSearch DocSearch-Button DocSearch-Button-Hover" aria-label="保存变更">
                <i class="el-icon-s-promotion"></i>
                <span class="DocSearch-Button-Container">保存变更</span>
              </button>
              <button
                  id="navCancelButton"
                  v-if="isEdit"
                  @click="cancelEdit"
                  type="button" class="DocSearch DocSearch-Button DocSearch-Button-Hover" aria-label="保存变更">
                <i class="el-icon-close"></i>
                <span class="DocSearch-Button-Container">取消变更</span>
              </button>
            </div>
          </div>
        </div>
        <div class="nav_main">
          <InfoNav
              id="navMain"
              :isEdit="isEdit"
              :nav-data.sync="config.navData"></InfoNav>
        </div>
      </div>
    </el-scrollbar>

  </div>

</template>

<script>
import InfoNav from "./components/InfoNav";
import request from "axios";
import EditTitle from "@/components/EditTitle.vue";

export default {
  name: 'App',
  components: {
    EditTitle,
    InfoNav
  },
  created() {
    request.get("/api/config").then(res => {
      this.config = res.data
      this.$nextTick(() => {
        if (this.$driver) {
          this.$driver.drive();
        }
      })
    })
  },
  data() {
    return {
      isEdit: false,
      config: {
        title: "",
        navData: []
      }
    }
  },
  computed: {},
  watch: {
    'config.title'() {
      document.title = this.config.title
    }
  },
  mounted() {
    this.$bus.$off("navEditButtonClick");
    this.$bus.$off("navCancelButtonClick")
    this.$bus.$on("navEditButtonClick", (res) => {
      this.editConfig();
    });
    this.$bus.$on("navCancelButtonClick", (res) => {
      this.cancelEdit()
    })
  },
  methods: {
    editConfig() {
      this.isEdit = true;
      this.configBak = JSON.parse(JSON.stringify(this.config))
    },
    cancelEdit() {
      this.isEdit = false;
      this.config = this.configBak;
      this.configBak = null
    },
    pushConfig() {
      request.post("/api/config", this.config).then(res => {
        this.isEdit = false;
      })
    }
  }
}
</script>

