<script>
export default {
  name: "EditTitle",
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    data: {
      type: String
    }
  },
  watch: {
    data(val) {
      this.title = val
    }
  },
  data() {
    return {
      visibleTitleEdit: false,
      title: ''
    }
  }
}
</script>

<template>
  <div>
    <button
        v-if="isEdit"
        @click="visibleTitleEdit = true"
        type="button" class="DocSearch DocSearch-Button DocSearch-Button-Hover" aria-label="修改标题">
      <i class="el-icon-edit"></i>
      <span class="DocSearch-Button-Container">修改标题</span>
    </button>
    <el-drawer
        title="修改标题内容"
        :append-to-body="true"
        :visible.sync="visibleTitleEdit">
      <div style="padding: 0 20px">
        <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入标题"
            v-model="title">
        </el-input>
        <div style="margin-top: 10px; display: flex; justify-content: flex-end">
          <el-button @click="visibleTitleEdit = false">取消</el-button>
          <el-button @click="$emit('update:data', title); visibleTitleEdit = false" type="primary">确定</el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<style scoped lang="scss">
.my-dialog {
  >> .el-dialog__header {
    display: none;
  }
}

</style>